import {
	mapState,
	mapActions,
	mapGetters,
	mapMutations
} from 'vuex'

export default {

	data() {
		return {
			imgUrlArray: []
		}
	},

	onLoad(option) {
		this.getTaskDetailsAction(option).then(data => {
			this.imgUrlArray = this.doneImgUrl()
		})
	},
	
	computed: {
		...mapState('home', [
			'taskDetails',
		])
	},
	
	components: {},

	methods: {
		...mapActions('home', ['getTaskDetailsAction']),
		...mapGetters('home', ['doneImgUrl']),
	}
}
